.button-wrapper{
    height:4em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: lighter;
    font-size:1.4em;
    background-color: #e0e1e6;
    color:#888;
    flex:1;
    outline: 1px solid #888;
}
.button-wrapper:active{
    color:black;
    background-color: #889;

}

.operator{
    background-color: #460646;
    color:#fff;
}